.data-specific {
    display: none;
}

select {
    width: 100%;
    padding: 3px 20px 3px 6px;
    height: 42px;
    border: 1px solid #ededee;
    color: #3a343a;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #fff;
    border-radius: 0;
    background-image: none;
    text-indent: 0.01px;
    text-overflow: "";
}

.filter--box {
    position: relative;
}

.filter--box:after {
    pointer-events: none;
    position: absolute;
    content: "";
    top: 22px;
    right: 3%;
    margin: -5px 0 0 -5px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 7px 4px 0 4px;
    border-color: #3a343a transparent transparent transparent;
}

.data-group {
    margin: 20px 0 40px;
}

.data-group .data-specific {
    -webkit-box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
            box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    padding: 20px 20px 15px;
}
